	module.exports = {"data":{"markdownRemark":{"html":"<p>Need to learn the basics of hacking? HackerOne offers Hacker101 - a free online course on the basics of hacking. The course is taught through video lessons. You don't have to go through the course in order, but you can simply watch the lessons on the topics that you want to learn about. </p>\n<p>There's a range of topics you can learn about. They range from writing reports, setting up Burp proxy, cookie security, to clickjacking and crypto attacks. </p>\n<p>Go to <a href=\"https://www.hacker101.com/\">https://www.hacker101.com/</a> to access all the course material. </p>\n<p>You can view the FAQ list at <a href=\"https://www.hackerone.com/hacker101\">https://www.hackerone.com/hacker101</a> or email hacker101@hackerone.com with questions or comments. </p>\n<p><img src=\"https://github.com/Hacker0x01/docs.hackerone.com/blob/master/docs/hackers/images/hacker101.png?raw=true\" alt=\"hacker101\"></p>","frontmatter":{"path":"/hackers/hacker101","title":"Hacker101"}}},"pathContext":{}}

